.me{
    
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position:absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem; 
    display: flex;/*extra */
    flex-wrap: wrap;/*  extra */
}

/*================SCROLL DOWN====================*/
.scroll_down{
    position: absolute;
    right:-2.3rem;
    bottom:5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ========MEDIA QUERIES(MEDIUM DEVICES)================*/
@media screen and (max-width:1024px){
  header{
    height : 68vh;
  }
}
/* ========MEDIA QUERIES(MEDIUM DEVICES)================*/
@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header__socials,
    .scroll_down{
        display: none;
    }
}